import axios from "axios";
import router from "../router";
import { Toast } from "vant";
// 创建axios实例
const service: any = axios.create({
  timeout: 10000 // 请求超时时间
});
let key1 = false;
let key2 = false;
// 添加request拦截器(请求)
service.interceptors.request.use(
  (config: any) => {
    Toast.loading({
      mask: false,
      duration: 0,
      forbidClick: true,
      message: "加载中"
    });
    setTimeout(() => {
      key2 = true;
      if (key1) {
        Toast.clear();
        key1 = false;
        key2 = false;
      }
    }, 500);
    return config;
  },
  (error: any) => {
    Toast.clear();
    Toast("请求错误!");
    Promise.reject(error);
  }
);
// 添加respone拦截器(响应)
service.interceptors.response.use(
  (response: any) => {
    key1 = true;
    if (key2) {
      Toast.clear();
      key1 = false;
      key2 = false;
    }
    let res: any = {};
    res.data = response.data;
    return res;
  },
  (error: any) => {
    Toast.clear();
    Toast("请求错误!");
    if (error.response && error.response.status == 404) {
      router.push("/blank.vue");
    }
    return Promise.reject(error.response);
  }
);
//不要忘记export
export default service;
